.Product{
    width: 400px;
    height: 400px;
    text-align: center;
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.Product h4 {
    color: gray;
    position: absolute;
    top: -10%;
    transition: all 0.5s ease-in-out;
    z-index: 10;
    opacity: 0
}

.Product img {
    width: 100%;
    height: 100%;
    transition: all 0.3s ease-in-out;
}

.Product:hover img{
    opacity: 0.5;
}

.Product:hover button {
    bottom: 0%;
    opacity: 1;
}

.Product:hover h4 {
    top: 5%;
    opacity: 1;
}


.Product button {
    width: 75%;
    position: absolute;
    bottom: -10%;
    transition: all 0.5s ease-in-out;
    z-index: 10;
    opacity: 0;
}


