.Carousel {
    width: 400px;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin:50px;
}

.CarouselThumbnails {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
}

.CarouselThumbnails li {
    list-style: none;
    margin:10px;
}
.Carousel .carousel-inner > .item > img {
    margin: 0 auto;
}